    top: 100%;
    left: 50%;
    display: block;
    margin-left: -8px;
    width: 0;
    height: 0;
    border-top: 8px solid color(var(--whitefull));
    border-right: 8px solid transparent;
    border-left: 8px solid transparent;
}

