/* 12 New Header */
/* header */
body {
    margin: 0;
}

.header {
    background-color: color(var(--whoblue));
    color: color(var(--whitefull));
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, .2);
    padding: 0 25px;
    position: fixed;
    width: 100%;
    z-index: 1000;
}

.header ul {
    margin: 4px 0 0 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
}

.header li a {
    color: color(var(--whitefull));
    display: block;
    margin: 20px 10px;
    text-decoration: none;
    transition: all 0.2s linear;
}

.header li a:after {
    background: color(var(--whitegrey));
    content: '';
    opacity: 0;
    display: block;
    height: 1px;
    position: relative;
    margin-top: 2px;
}

.header li a:hover:after {
    opacity: 0.8;
    transition: all 0.35s ease-in-out;
}

.header .logo {
    display: block;
    float: left;
    color: color(var(--whitefull));
    font-size: 1.5em;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    padding: 15px 0;
    text-decoration: none;
}

.whitespace {
    padding: 0;
    margin: 50px;
    display: block;
}

.active a {
    font-weight:800;
}
.active a:after {
    opacity: 1 !important;
    background: #ffd31d !important;
    height: 2px !important;
}
.active:hover a:after {
    background: #fff !important;
    height: 1px !important;
    transition: .3s all ease-in-out;
}
/* menu */

.header .menu {
    clear: both;
    max-height: 0;
    transition: max-height 0.2s ease-out;
    display: inline-flex;
}

.menu-list {
    z-index:0;
    clear: both;
    transition: max-height 0.2s ease-out;
}

/* menu icon */

.header .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    padding: 28px 20px;
    position: relative;
    user-select: none;
}

.header .menu-icon .navicon {
    background: color(var(--whitefull));
    display: block;
    height: 2px;
    position: relative;
    transition: background 0.2s ease-out;
    width: 18px;
}

.header .menu-icon .navicon:after,
.header .menu-icon .navicon:before {
    background: color(var(--whitefull));
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all 0.2s ease-out;
    width: 100%;
}

.header .menu-icon .navicon:before {
    top: 5px;
}

.header .menu-icon .navicon:after {
    top: -5px;
}

/* menu btn */

.header .menu-btn {
    display: none;
}

.header .menu-btn:checked~.menu {
    max-height: 350px;
}

.header .menu-btn:checked~.menu-icon .navicon {
    background: transparent;
}

.header .menu-btn:checked~.menu-icon .navicon:before {
    transform: rotate(-45deg);
}

.header .menu-btn:checked~.menu-icon .navicon:after {
    transform: rotate(45deg);
}

.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after,
.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before {
    top: 0;
}

.transparent {
    background: rgba(0, 0, 0, 0.8);
    box-shadow: none;
}

/*  Ribbon */
.ribbon {
    margin: 10px 5%
}

.ribbon-row {
    display: -ms-flexbox;
    /* IE10 */
    display: flex;
    -ms-flex-wrap: wrap;
    /* IE10 */
    flex-wrap: wrap;
    padding: 0 4px;
}

.ribbon-column {
    -ms-flex: 5.88%;
    /* IE10 */
    flex: 5.88%;
    max-width: 5.88%;
    padding: 0 2px;
    height: 100%;
    width: 100%;
    -ms-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.ribbon-column a {
    text-decoration: none;
}

.ribbon-column img {
    margin-top: 8px;
    vertical-align: middle;
    width: 100%;
}

.ribbon-sm {
    display: flex;
}

.ribbon-column-sm {
    margin: 1px 0;
    height: 100%;
    width: 100%;
    -ms-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.ribbon-column-sm img {
    margin: 2px;
    vertical-align: middle;
    width: auto;
    height: auto;
    max-height: 64px;
}

.ribbon-column-sm:hover,
.ribbon-column:hover {
    z-index: 2;
    transform: scale(1.5, 1.5);
    cursor: pointer;
}

.ribbon-column:hover .middle {
    opacity: 1;
}

.ribbon-caption {
    margin: 20px 0;
}

.ribbon-head {
    margin-top: 25px;
    font-weight: 600;
}

.middle {
    display: block;
    opacity: 0;
    font-size: 7pt;
    font-weight: 600;
}

#language-tooltip {
    position: absolute;
    top: 100%;
    right: 475px;
    z-index: 1001;
    display: block;
    padding: 2px 8px;
    color: color(var(--whitefull));
    font-size: 1.2rem;
    letter-spacing: 0.2px;
    white-space: nowrap;
    background: #15171a;
    border-radius: 3px;
    box-shadow: 0 12px 26px rgba(39, 44, 49, .08), 1px 3px 8px rgba(39, 44, 49, .03);
    opacity: 0;
    transition: all 0.35s cubic-bezier(.4, .01, .165, .99);
    transform: translateY(6px);
    pointer-events: none;
}

.lang-switch {
    z-index:2;
}
.lang-switch:hover+#language-tooltip {
    opacity: 1.0;
    transform: translateY(0px);
}

.image-responsive {
    width: 100%;
    height: auto;
}

/*Sliders etc*/

.hero-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    color: white;
    font-size: 32px;
    font-weight: 600;
}

.blockquote {
    display: block;
    min-height: 20vh;
    height: 20vh;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 2.5rem;
    font-weight: 600;
    padding: 50px 150px 150px;
    line-height: 25pt;
    color: #333;
    height: 20vh;
    margin-bottom: 20px;
    border-top: 4px solid color(var(--whoblue));
    border-bottom: 4px solid color(var(--whoblue));
}

.blockquote-title {
    font-size: 4rem;
    color: color(var(--whoblue));
}

.index-intro {
    text-align: center;
    font-family: 'Monteserrat', sans-serif;
    font-size: 2rem;
    font-weight: 800;
    padding: 2% 10%;
    margin: 15px 0 0 0;
    color: #666;
