/* ---------- */
.allSlides {
    height: 100vh;
    background: #000;
}

.header-img {
    height: 100vh;
    max-height: 100vh;
    margin: 0;
    padding: 0;
    object-fit:cover;
}

/* Slideshow container */
.slideshow-container {
    max-width: 100%;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    margin: 0;
    padding: 0;
}

/* Hide the images by default */
.mySlides {
    display: block;
    margin: 0;
    padding: 0;
}

/* Next & previous buttons */
.next,
.prev {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    padding: 16px;
    color: #fff;
    font-size: 45px;
    transition: 0.3s ease;
    border-radius: 0 3px 3px 0;
    user-select: none;
}

/* Position the "next button" to the right */
.next {
    right: 0;
    border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.next:hover,
.prev:hover {
    background-color: rgba(0, 0, 0, 0.8);
    text-decoration: none;
}

/* Caption text */
.text {
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    padding: 2rem 12rem;
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: left;
    backdrop-filter: blur(3px);
    background: linear-gradient(rgba(0, 0, 0, 0.0), rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.8));
}

.text:focus,
.text:hover {
    color: color(var(--lgreen));
    transition: 0.3s;
}

.head-title {
    border-left: 3px solid color(var(--whoblue));
    padding: 0 0 0 1rem;
    color: #fff;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: 3.5rem;
    margin: 0 0 0.75rem;
    text-transform: uppercase;
    animation-delay: 0.5s;
}

.head-desc {
    font-weight: 400;
    max-width: 60%;
    overflow: hidden;
    margin-bottom: 2px;
    text-overflow: ellipsis;
    white-space: nowrap;
    animation-delay: 2s;
}

.caps {
    border-bottom: 1px solid transparent;
    text-transform: uppercase;
    margin: 0;
    float: right;
    font-weight: 600;
    animation-delay: 3s;
}

.caps:hover {
    border-bottom: 1px solid color(var(--whoblue));
    transition: 0.3s;
}

.mySlides:hover .head-title {
    border-left: 3px solid color(var(--lgreen));
    transition: 0.3s;
}

.mySlides:hover .head-desc {
    color: color(var(--lgreen));
    transition: 0.3s;
}

/* Fading animation */
.fade {
    -webkit-animation-name: slideInRight;
    -webkit-animation-duration: 0.5s;
    animation-name: slideInRight;
    animation-duration: 0.5s;
}

.featurer-top {
    position: absolute;
    top: 5px;
    left: 5px;
    padding: 2px 10px;
    z-index: 2;
}
.featurer-top svg{
    height:250px;
}
.featurer {
    position: absolute;
    bottom: 5px;
    left: 5px;
    color: #111;
    background-color: color(var(--whitefull));
    border-radius: 2rem;
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: 0.5px;
    font-family: 'Montserrat'sans-serif;
    padding: 2px 10px;
    text-transform: uppercase;
    z-index: 2;
}

.featurer span {
    display: inline-block;
}

/**********************/
.footer-distributed {
    background-color: #292c2f;
    width: 100%;
    text-align: left;
    font: bold 16px 'Montserrat'sans-serif;
    padding: 55px 50px 0;
    margin-top: 20px;
}

.footer-distributed .footer-center,
.footer-distributed .footer-left,
.footer-distributed .footer-right {
    display: inline-block;
    vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left {
    width: 40%;
}

/* The company logo */

.footer-distributed h3 {
    color: #ffffff;

    font-weight: 600;
    margin: 0;
}

.footer-distributed h3 span {
    color: color(var(--whoblue));
}

/* Footer links */

.footer-distributed .footer-links {
    color: #fff;
    margin: 20px 0 12px;
    padding: 0;
    font-size: 1.2rem;
    text-align: center;
    letter-spacing: .3pt;
}

.footer-distributed .footer-links a {
    display: inline-block;
    line-height: 1.8;
    text-decoration: none;
    color: #fff;
    transition: .3s all ease-in;
}

.footer-links a:hover {
