/* Footer Center */

.footer-distributed .footer-center {
    width: 25%;
    text-align: center;
}

.footer-distributed .footer-center i {
    background-color: #33383b;
    color: var(--whitefull);
    font-size: 25px;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    text-align: center;
    line-height: 42px;
    vertical-align: middle;
}

.footer-distributed .footer-center i.fa-envelope {
