    font-weight: normal;
    font-size: 14px;
    line-height: 2;
}

.footer-distributed .footer-center p a {
    color: var(--whoblue);
    text-decoration: none;
}

.partners {
    margin-top: 50px;
}

.partners a {
    width: 150px;
    height: 50px;
    min-height: 50px;
    max-height: 50px;
    margin: 20px 0;
    background: #fefefe;
    padding: 5px;
