}

.tagsp {
    height: 100%;
}

/* Footer Right */

.footer-distributed .footer-right {
    width: 32%;
    text-align: right;
}

.footer-distributed .footer-company-about {
    line-height: 20px;
    color: color(var(--whitegrey));
    font-size: 14px;
    font-weight: 600;
    margin: 20px 0 0;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1px;
}

.footer-company-about a {
    color: color(var(--midgrey) l(+15%));
    text-decoration: none;
    font-weight: 400;
    transition: color 0.3s linear;
}

.footer-company-about a:hover {
    color: var(--whoblue);
}

.footer-company-about path,
.footer-company-about svg {
