.footer-distributed .footer-icons a {
    display: inline-block;
    width: 48px;
    height: 48px;
    cursor: pointer;
    background-color: #33383b;
    border-radius: 2px;
    color: #ffffff;
    text-align: center;
    line-height: 48px;
