/* MailChimp Form Embed Code - Horizontal Super Slim - 12/16/2015 v10.7
Adapted from: http://blog.heyimcat.com/universal-signup-form/ */
#mc_embed_signup {
    margin-top: 100px;
}

#mc-embedded-subscribe-form {
    position: relative;
    display: block;
    height: 40px;
    border-radius: 20px;
    background: white;
    margin: 0 auto;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    overflow: hidden;
    max-width: 350px;
}

#mc-embedded-subscribe-form input {
    line-height: 40px;
    padding: 0;
    border: none;
    outline: none;
    background: #eee;
    transition: .2s all ease-in;
}

#mc-embedded-subscribe-form input:not([type=submit]) {
    width: 100%;
    display: block;
    padding: 0 150px 0 30px;
}

#mc-embedded-subscribe-form input:not([type=email]) {
    position: absolute;
    height: 36px;
    line-height: 36px;
    top: 2px;
    right: 2px;
    border-radius: 18px;
    padding: 0 18px;
    background: #333;
    color: white;
    font-weight: 800;
    cursor: pointer;
}

#mc-embedded-subscribe-form input:not([type=email]):hover {
    background: color(var(--whoblue));
}

#mc-embedded-subscribe-form input:focus {
    outline: none;
    background: #fff;
}


/* Responsive layout - makes a two column-layout instead of four columns */
/* 48em = 768px */
@media (min-width: 48em) {

    .header li {
        float: left;
    }

    .header li a {
        padding: 0.5rem 1rem;
        margin: 0;
    }

    .header .menu {
        clear: none;
        float: right;
        max-height: none;
    }

    .header .menu-icon {
        display: none;
    }

}

@media (max-width: 880px) {

    .footer-distributed {
        font: bold 14px sans-serif;
    }

    .header li a {
        padding: 0.5rem;
    }

    .footer-distributed .footer-center,
    .footer-distributed .footer-left,
    .footer-distributed .footer-right {
        display: block;
        width: 100%;
        margin-bottom: 40px;
        text-align: center;
    }

    .footer-distributed .footer-center i {
        margin-left: 0;
    }

    .blockquote {
        padding: 5%;
        font-size: 2rem;
        line-height: 20pt;
    }

    .blockquote-title {
        font-size: 2.2rem;
    }

    .text {
        padding: 8px 30px;
    }

    hr {
        margin: 2rem;
    }

    .fade {
        -webkit-animation-name: slideInDown;
        -webkit-animation-duration: 1s;
        animation-name: slideInDown;
        animation-duration: 1s;
    }

    .ribbon-column {
        -ms-flex: 11%;
        flex: 11%;
        max-width: 11%;
    }

    .header .logo img {
        width: 20rem;
    }

}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */

@media screen and (max-width: 600px) {

    .post-card-image {
        object-fit: cover;
    }

    .blockquote,
    .index-intro {
        padding: 2% 5%;
        font-size: 1.2rem;
        margin: 0;
    }

    .index-intro {
        padding: 8%;
    }

    .blockquote-title {
        font-size: 1.5rem;
    }


    .head-desc {
        max-width: 90%;
    }

    .header ul {
        margin: 0;
        float: left;
        text-align: left;
        width: 75vw;
    }

    .header .logo {
        padding: 0;
        font-size: 1.2em;
    }

    .header .logo img {
        width: 22rem;
        vertical-align: bottom;
        padding-top: 20px;
    }

    .fade {
        -webkit-animation-name: flipInY;
        -webkit-animation-duration: 1s;
        animation-name: flipInY;
        animation-duration: 1s;
    }
    .featurer-top svg {
        height: 150px;
    }
    .ribbon-column {
        -ms-flex: 33%;
        flex: 33%;
        max-width: 33%;
    }

    .post-full-byline {
        -ms-flex: 51%;
        flex: 51%;
        flex-wrap: wrap;
    }
    .menu {
        float: right;
        margin-right: 0 !important;
        right: 0;
        top: 50px;
        display: none;
    }
    .header .menu, .menu-list {
    width:100%;
    }

    .head-title {
        font-size:unset;
    }

    .menu-btn:checked~.menu {
        display: block;
    }

    .flag {
        float: right;
        display: none;
    }

    .flag:hover+#language-tooltip {
        opacity: 0;
    }

    .menu-btn:checked~.menu .flag {
        display: inline-block;
    }

    .map {
        margin: 5px;
    }

    #mapdiv {
        height: 250px;
        width: 100%;
    }

    .navigate {
        display: block;
    }
}